export default {
	name: 'HelloWorld',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App',
			switchValue1: false,
			switchValue2: false,
			switchValue3: false,
			switchValue4: false,
			switchValue5: false,
			switchValue6: false,
			switchValue7: false,
			switchValue8: false,
			switchValue9: false,
			switchValue10: false,
			switchValue11: false,
			switchValue12: false,
			switchValue13: false,
			switchValue14: false,
			switchValue15: false,
			switchDisabled: false,
			text8: '',
			micShow: false,
			menus: [
				{
					label: '确定',
  					type: 'primary',
					value: 'confirm'
					// clickMenuHide: false
				},
				{
					label: 'warning',
  					type: 'warning',
					value: 'warning'
				},
				{
					label: 'disabled',
  					type: 'disabled',
					value: 'disabled'
				},
				{
					label: '<span style="color:blue;">Default+自定义</span>'
				}
			]
