	},
	created () {
		// this.$WLoading()
		// this.$WLoading('加载中...')
		// this.$WLoading({
		// 	text: '加载中...',
		// 	loadingType: 0
		// })
		// setTimeout(() => {
		// 	this.$Wloading.end()
		// }, 5000)
	},
	beforeRouteLeave (to, from, next) {
		if (to.path === '/home') {
			this.$parent.headerTitle = 'home'
			this.$parent.selectTab = 0
		}
		next()
	},
	methods: {
		switchChange1 (msg) {
			this.$WLoading('加载中...').then(() => {
				console.log('这是开启的回调')
			})
			setTimeout(() => {
				this.$WLoading.end().then(() => {
					console.log('这是关闭的回调')
				})
				this.$WLoading('再一次加载中...').then(() => {
					console.log(this)
				})
			}, 2000)
		},
		switchChange2 (msg) {
			this.$WToast({
				message: '一二三四五六七八九十',
				position: 'top',
				type: 'cancel',
				maskShow: false,
				duration: 1000000
			})
		},
		switchChange3 (msg) {
			this.$WToast({
				message: 'middle提示',
				position: 'middle',
				type: 'warning',
				maskShow: false
			})
		},
		switchChange4 (msg) {
			this.$WToast({
				message: 'bottom提示',
				position: 'bottom',
				maskShow: true
			})
		},
		switchChange5 (msg) {
			this.$WToast({
				message: '默认提示',
				maskShow: false,
				duration: 1000
			})
		},
		switchChange6 (msg) {
			this.$WToast({
				message: '文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示',
				maskShow: false,
				type: 'text',
				width: '300'
			})
		},
		onHide (msg) {
			console.log('onhide')
		},
		onShow (msg) {
			console.log('onshow')
		},
		switchChange8 (val) {
			if (val) {
		        this.tick(0, (percent) => {
		          if (percent === 100) {
		            this.switchValue8 = false
		            return
		          }
		          this.text8 = `${percent}%`
		        })
		    }
		},
		tick (i, cb) {
			let _this = this
			setTimeout(function () {
		    	i++
			    cb(i)
			    if (i < 100) {
			      _this.tick(i, cb)
			    }
		  	}, 25)
		},
		switchChange10 (val) {
			// this.$WConfirm('这是第一个')
			this.$WConfirm({
				title: '提示啊',
				message: '这是一个提示'
			}).then(() => {
				console.log('这是confirm的回调')
				this.switchValue10 = false
			}, () => {
				console.log('这是cancel的回调')
				this.switchValue10 = false
			})
			// this.$WConfirm('这是第二个').then(() => {
			// 	console.log('这是confirm的回调2')
			// }, () => {
			// 	console.log('这是cancel的回调2')
			// })
			// setTimeout(() => {
			// 	this.$WConfirm.close().then(() => {
			// 		console.log('我就试试close好使不好使')
			// 	})
			// }, 2000)
		},
		switchChange11 (val) {
			this.$WConfirm({
				message: '这是第三个',
				titleColor: 'red',
				messageColor: 'blue',
				clickMaskHide: true
			}).then(() => {
				console.log('这是confirm的回调')
				this.switchValue11 = false
			}, () => {
				console.log('这是cancel的回调')
				this.switchValue11 = false
			})
		},
		touchstart () {
			this.micShow = true
		},
		touchend () {
			this.micshow = false
		},
		onConfirm () {
			console.log('confirm回调')
		},
		onCancel () {
			console.log('cancel回调')
		},
		onMask () {
			console.log('onMask回调')
		},
		switchChange13 () {
			this.$WAlert({
				message: '这是一个alert框',
				clickMaskHide: true
			}).then(() => {
				console.log('alert回调')
				this.switchValue13 = false
			})
		},
		buttonClick () {
			console.log('触发button点击')
		},
		itemClick () {
			console.log(arguments)
		}
	},
	computed: {
		micClass () {
			return !this.micShow ? 'micBlack' : 'micRed'
		}
	}
}
