<template>
	<div class="home-content">
		<div class="wind-logo">
			<img src="../assets/wind-logo.png" alt="">
			<p class="wind-notice">v1.0.0</p>
			<div class="cell-group">
				<w-cell title="Live Demo" @click.native="liveDemoCellClick" link="/helloWorld">
					<img slot="cell-icon" class="cell-icon" src="../assets/demo.png"></img>
				</w-cell>
				<w-cell title="Github" value="star me">
					<img slot="cell-icon" class="cell-icon" src="../assets/GitHub.png"></img>
				</w-cell>
				<w-cell title="Live Demo" value="这是value" :is-link="false" label="这是标题下面的信息" :disabled="true">
					<img slot="cell-icon" class="github-icon" src="../assets/demo.png"></img>
					<span slot='cell-value'>这也是value</span>
				</w-cell>
				<w-cell title="githubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslotgithubslot">
					<img slot="cell-icon" class="github-icon" src="../assets/demo.png"></img>
					<span slot="cell-title">标题</span>
					111
					<span slot="cell-value">star mestar mestar mestar mestar mestar mestar mestar mestar memestar memestar me</span>
				</w-cell>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'home',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	created () {
	},
	methods: {
		liveDemoCellClick () {
			this.$parent.headerTitle = 'demo'
			this.$parent.selectTab = 1
		}
	},
	computed: {
	}
}
</script>

<style>
.home-content {
	width:100%;
}
.wind-logo {
	margin-top: 15px;
}
.wind-notice {
	line-height: 40px;
	font-size: 18px;
}
.cell-group {
	margin-top: 25px;
}
.cell-icon {
	width: 80%;
	height: 80%;
}
</style>



