<template>
	<div>
		<w-swipe :height="200" :defaultIndex="0"  :showIndexDot="true" :loop="true" :prevent="false" :stopPropagation="true" @on-index-change="changeItem" @on-item-click="onItemClick">
			<w-swipe-item class="slide1" :line-height="200">
				<a href="http://www.baidu.com" target="_blank">item 1</a>
			</w-swipe-item>
			<w-swipe-item class="slide2">item 2</w-swipe-item>
			<w-swipe-item class="slide3">item 3</w-swipe-item>
		</w-swipe>
	</div>
</template>

<script>

export default {
	name: 'SwipeDemo',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	created () {
	},
	methods: {
		changeItem (newIndex, oldIndex) {
			// console.log(newIndex, oldIndex)
		},
		onItemClick (currentIndex) {
			console.log(currentIndex)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.slide1 {
	background-color: #0089dc;
	color: #fff;
	height: 200px;
}
.slide2 {
	background-color: #ffd705;
	color: #000;
	height: 300px;
}
.slide3 {
	background-color: #ff2d4b;
	color: #fff;
	height: 200px;
}
</style>



