<template>
	<div>
		<w-picker :data="pickerData" v-model="pickerValue" :rowCount='7'></w-picker>
		<w-cell title="您选择的是:">{{`${pickerValue[0]},${pickerValue[1]},${pickerValue[2]===undefined?'':pickerValue[2]}`}}</w-cell>
		<w-button type='primary' @click="setPickerData()">改变数据</w-button>
		<w-button type='primary' @click="setPickerValue(['法国','德国'])">设置法国,德国</w-button>
	</div>
</template>

<script>

export default {
	name: 'PickerDemo',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App',
			pickerData0: ['中国', '美国', '英国', '日本', '韩国', '意大利', '法国', '德国', '韩国', '意大利', '法国', '德国'],
			pickerData: [
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国', '德国', '1', '2', '3', '4', '5', '6', '7', '8'],
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国', '德国']
			],
			pickerData1: [
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国'],
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国'],
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国']
			],
			pickerValue: []
		}
	},
	created () {
	},
	methods: {
		setPickerValue (pickerValue) {
			this.pickerValue = pickerValue
		},
		setPickerData () {
			this.pickerData = this.pickerData1
			this.pickerValue = []
		}
	}
}
</script>

<style>

</style>



