import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import IndexListDemo from '@/components/IndexListDemo'
import SwipeDemo from '@/components/SwipeDemo'
import PickerDemo from '@/components/PickerDemo'
import Home from '../pages/Home'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
		  path: '/home',
		  name: 'home',
		  component: Home,
		  meta: {
			title: '主界面'
		  }
		}, {
			path: '/helloWorld',
			name: 'HelloWorld',
			component: HelloWorld,
			meta: {
				title: 'HelloWorld'
			}
		}, {
			path: '/indexListDemo',
			name: 'IndexListDemo',
			component: IndexListDemo,
			meta: {
				title: 'IndexListDemo'
			}
		}, {
			path: '/swipeDemo',
			name: 'SwipeDemo',
			component: SwipeDemo,
			meta: {
				title: 'SwipeDemo'
			}
		}, {
			path: '/pickerDemo',
			name: 'PickerDemo',
			component: PickerDemo,
			meta: {
				title: 'PickerDemo'
			}
		}, {
	      path: '/',
	      redirect: '/home'
	    }, {
	      path: '*',
	      redirect: '/home'
	    }
	]
})



