<template>
	<div class="wind-picker">
		<div class="wind-picker-items" :style="{
				height:`${itemHeight * currentRowCount}px`
			}">
			<picker-item-group v-for="(itemData, index) in currentData" :itemData="itemData" v-model="currentValue[index]"	:key='index' :rowCount='currentRowCount' :itemHeight='itemHeight'></picker-item-group>
		</div>
	</div>
</template>
<script>
/**
 * w-switch by shang 2017/11/1
 * @desc 切换按钮
 * @param {value} [boolean] - 绑定值，支持双向绑定
 * @param {slot}  [str] - 显示内容
 * @param {disabled} [slot] - 是否禁用
 *
 * @example
 * <w-switch v-model="value"></w-switch>
 * 1.一般使用绑定value即可实现双向绑定
 * 2.如有权限需求 绑定 disabled 即可,禁用状态下 按钮会有0.6的透明度
 * 3.需要根据开启/关闭 执行函数 绑定change 即可 v-on:change
 */
const ITEM_HEIGHT = 34
const ROW_COUNT = 7
import PickerItemGroup from './picker-item.vue'
export default {
	name: 'w-picker',
	props: {
		data: Array,
		value: Array,
		itemHeight:{
			type: Number,
        	default: ITEM_HEIGHT
		},
		rowCount: {
	    	type: Number,
        	default: ROW_COUNT
	    }
	},
	components: {
      PickerItemGroup: PickerItemGroup
    },
    data () {
	    return {
	      currentData: this.data,
	      currentValue: this.value,
	      currentRowCount: this.rowCount
	    }
	},
    created () {
    	if (this.currentRowCount % 2 === 0) {
    		this.currentRowCount = this.currentRowCount + 1
    	}
    	if({}.toString.call(this.data[0]) !== '[object Array]') {
	    	let array = []
	    	array.push(this.data)
	    	this.currentData = array
		}
		if({}.toString.call(this.value) !== '[object Array]') {
	    	this.currentValue = []
		}

	},
	watch: {
		value (val) {
			console.log('picker change',val)
			if({}.toString.call(val) !== '[object Array]') {
		    	this.currentValue = []
			} else {
				this.currentValue = val
			}
		},
		data (data) {
			console.log('change data')
			if({}.toString.call(data[0]) !== '[object Array]') {
		    	let array = []
		    	array.push(data)
		    	this.currentData = array
			} else {
				this.currentData = data
			}
		},
		currentData () {
			console.log('change currentdata')
		},
		currentValue () {
			console.log('change currentValue')
		}
	}
}
</script>

<style lang="css">
.wind-picker {

}
.wind-picker .wind-picker-items {
    position: relative;
    overflow: hidden;
    width: 100%;
    display: flex;
}
</style>



